* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 15px;

    --main: #2a607c;
    --main2: #7da4b8;
    /* --main2: #c7ecff; */
    --main3: #193b4d;
    --white-text: white;

    --blue1: ##00ABE1;
    --blue2: #161F6D;

    --plant1: #5DAA68;
    --plant2: #3F6844;
    --plant3: #FAF1CF;


}

footer {
    /* (A1) FIXED AT BOTTOM */
    position: fixed;
    bottom: 0;
    left: 0;
    /* (A2) DIMENSIONS */
    width: 100%;
    height: 40px;
    padding: 0 5px;
    /* (A3) CENTER TEXT */
    display: flex;
    align-items: center;
    justify-content: center;

    /* display: flex;
    justify-content: space-between;
    align-items: center; */
    
    /* flex-wrap: wrap; */
    background-color: var(--main);

    /* height: 50px; */

    color: var(--white-text);


}

/* .box { border: 1px solid black; } */

/* RANDOM STYLES START */

.white-text {
    color:white;
}

.flex-around {
    display: flex;
    justify-content: space-around;
}
.center {
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.section-container { 
    padding-top: 10px; 
    padding-bottom: 10px; 
    /* border: 1px solid black */
}
.section-title {
    display: flex;
    justify-content: flex-end;

    border-right: 3px solid black;

    width: 30%;
    font-size: 25px;

    padding-right: 20px;
    padding-left: 20px;

    margin-right: 20px;
}
.content-container {
    display: flex;
    justify-content: space-around;

    flex-direction: column;
    width: 70%;
}
.content-right-p {
    margin: 10px;
}
.horizontal-center {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.backlog {
    display: none;
}

/* Header */
header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    background-image: url("../Images/lake.jpg");
    background-size: cover;
    background-position: center;

    height: 150px;

    color: var(--white-text);

}
header h1 {
    display: inline-block;
    text-align: center;
    font-size: 48px;

    margin-left: 50px;
}
header nav {
    padding-top: 15px;
    margin-right: 20px;
    float: right;
    font-size: 20px;

    margin-right: 50px;

}
header nav ul {
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}
header nav ul li {
    margin-left: 25px;
}
a {
    color: var(--white-text);
    text-decoration: none;
}

/* Hero */
.hero {
    background-image: url("../Images/lake.jpg");
    height: 200px;
    width: 100%;
    margin-bottom: 25px;
    background-size: cover;
    background-position: center;

    display: flex;
    justify-content: center;
    align-items: flex-end;
}
#hero-text {
    font-size: 40px;
    padding: 5px;
}

/* About Me */    
#cbm-profile {
    background-image: url(../Images/profile-pic.jpg);
    /* height: auto; */
    /* margin: 10px; */
    /* max-width: 300px; */
    /* max-height: 300px; */
    background-size: cover; 
    background-position: center;
    /* height: 100%;
    width: 100%; */
    
    height: 400px;
    width: 400px;

}


/* Work section Start */
.grid {
    display: grid;
    /* grid-template-columns: 50% 50%; */
    grid-template-columns: 25% 25% 25% 25%;
}

.text-box {
    background-color: var(--main);
    color: var(--white-text);
}

.colorMain {
    background-color: var(--main);
    color: var(--white-text);

}

.text-center {
    text-align: center;
    
}

.projectLinks {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.flex-col {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.projLink {
    height: 35px;
    font-size: 1rem;
    width: 8rem;
}
.resumeWidth {
    /* display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; */
    /* align-items: center; */

    border: 1px solid black;
    margin: 1px;
    /* padding-top: 20px;
    margin-top: 20px; */


    width: 90%;
}
.projTitle {
    height: 40px;
    /* font-size: 1.5rem; */
    /* width: 8rem; */

}

.projLink:hover {
    background-color: var(--main2);
    /* color: var(--white-text); */
}

.projectCard {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    height: 300px;
    /* width: 100%; */

    margin: 10px;
    border: 5px solid var(--main);

    background-position: center top;
    background-size: cover;
}
.projectCard h3 {
    font-size: 30px;
    padding: 5px;
}
.projectCard h4 {
    font-size: 15px;
    padding: 0 5px 5px 5px;
}
.hoverFade:hover { opacity: 50%; }
.linkHover:hover { opacity: 50%; }



