/* Start Contact Me */
.contact-li { margin-left: 15px; }

.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}
.flex-align-center {
    display: flex;
    /* justify-content: center; */
    align-items: center;
}
footer {
    height: 50px;
    /* background-color: var(--main); */
    background-color: var(--main);
}
footer nav { width: 100%; }
footer nav ul {

    list-style-type: none;
    color: white;


    /* border: 1px solid black; */

}

.hide {
    background-color: blueviolet;
    /* display: none; */
}

.pointer { cursor: pointer; }

.hoverColorChange:hover { color: var(--main2); }

@media screen and (max-width: 1300px) { .grid { grid-template-columns: 33% 33% 33%; } }
@media screen and (max-width: 900px) { .grid { grid-template-columns: 50% 50%; }}
@media screen and (max-width: 860px) {
    header {
        justify-content: space-around;
    }
    header h1 {
        margin-left: 0px;
    }
    header nav {
        margin-right: 0px;
    }
}
@media screen and (max-width: 575px) {
    header {
        justify-content: center;
        align-items: center;
    }
    .section-container {
        flex-direction: column;
        align-items: center;
    }
    .section-title {
        display: flex;
        justify-content: left;
        /* width: 70%; */
        border-right: 0px solid black;
        padding-right: 0px;
        margin-right: 0px;
        padding-left: 0px;
    }
    .content-container {
        align-items: center;

        border-top: 3px solid black;
        padding-top: 20px;
        margin-top: 20px;
    }
    .grid { grid-template-columns: 100%; }
    .image-pair {
        flex-direction: column;
    }
    #contact-nav {
        padding-bottom: 20px;
    }
}
